// :: 8일차 문제
// 조건부 렌더링(2) - 삼항연산자

import {useState} from 'react';
import styles from './day_8_problem.module.css';

function App (){

    const [number, setNum] = useState(0);
    function checkTF(){
        if(number === 0){
            setNum(1)
        }
        else{
            setNum(0);
        }
    }
    return (
        <div className={styles.box}>
            {number === 0 ?<span className={styles.True}>True입니당</span>:<span className={styles.False}>False입니당</span>}
            <br/><button onClick={checkTF}>색바꾸기</button>
        </div>
    );
}

export default App;